export function storeSet(id, key, val) {
  let seller = window.localStorage.getItem('__seller__');
  if (!seller) {
    seller = {};
    seller[id] = {};
  } else {
    seller = JSON.parse(seller);
    if (!seller[id]) {
      seller[id] = {};
    }
  }
  seller[id][key] = val;
  window.localStorage.setItem('__seller__', JSON.stringify(seller));
}
export function storeGet(id, key) {
  let seller = window.localStorage.getItem('__seller__');
  if (seller) {
    seller = JSON.parse(seller);
    if (seller[id] && seller[id][key]) {
      return seller[id][key];
    }
  }
  return false;
}



