<template>
  <div id="app">
    <Heade :seller="seller"></Heade>
    <div class="tab">
      <div class="tab-item">
        <a v-link="{path: '/goods'}">商品</a>
      </div>
      <div class="tab-item">
        <a v-link="{path: '/ratings'}">评价</a>
      </div>
      <div class="tab-item">
        <a v-link="{path: '/seller'}">商家</a>
      </div>
    </div>
      <router-view :seller="seller" keep-alive></router-view>
  </div>
</template>
<script>
  import Heade from './components/header/Heade.vue'
  export default {
    data() {
      return {
        seller: {
          id: this.getUrlId().id
        }
      };
    },
    ready() {
      this.$http.get('/api/seller?id=' + this.seller.id).then((res) => {
        res = res.data;
        if (res.errno === 0) {
          this.seller = Object.assign({}, this.seller, res.data);
        }
      });
    },
    components: {
      Heade
    },
    methods: {
      getUrlId() {
        let str = window.location.search;
        let re = /[?&][^?&]+=[^?&]+/g;
        let arr = str.match(re);
        let obj = {};
        if (!arr) {
          obj.id = 123;
        } else {
          arr.forEach((item) => {
            let tmp = item.substring(1).split('=');
            obj[tmp[0]] = tmp[1];
          });
        }
        return obj;
      }
    }
  }
</script>
<style lang="scss">
  @import './commom/sass/variable.scss';
  @import './commom/sass/mixin.scss';
  .tab {
    display: flex;
    width: 100%;
    height: 80rem/$r;
    line-height:80rem/$r;
    @include border-1px($color7-17-27-1);
    .tab-item {
      flex: 1;
      text-align: center;
      & > a {
        display: inline;
        color: rbg(77, 85 ,93);
        @extend %f-size28;
        &.v-link-active {
          color: orange;
        }
      }
    }
  }
</style>



