<template>
  <div class="food" v-show="showFlag" v-el:food>
    <div class="food-content">
      <div class="img-header">
        <img :src="food.image">
        <div class="back" @click="hide">
          <span class="icon-arrow_lift"></span>
        </div>
      </div>
      <div class="content">
        <h1 class="title">{{food.name}}</h1>
        <div class="detail">
          <span>月售{{food.sellCount}}</span>
          <span>好评率{{food.rating}}%</span>
        </div>
        <div class="price">
          <span class="now">￥{{food.price}}</span>
          <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
        </div>
        <div class="cartcontrol-wrap">
          <cartcontrol :food="food"></cartcontrol>
        </div>
        <div class="buy animated" v-show="!food.count" transition="fade" @click="addFirst">加入购物车</div>
    </div>
    <split v-show="food.info"></split>
    <div class="info" v-show="food.info">
      <h1 class="title">商品介绍</h1>
      <p class="text">{{food.info}}</p>
    </div>
    <split></split>
    <div class="rating">
      <h1 class="title">商品评价</h1>
      <ratingselect :ratings="food.ratings" :select-type="selectType" :only-content="onlyContent" :desc="desc"></ratingselect>
      <div class="rating-content-wrap">
        <ul v-show="food.ratings&&food.ratings.length">
          <li v-for="rating in food.ratings" class="rating-item" v-show="filter(rating.rateType,rating.text)">
            <div class="user">
              <span class="username">{{rating.username}}</span>
              <img class="avatar" :src="rating.avatar">
            </div>
            <div class="time">{{rating.rateTime | time}}</div>
            <p class="text">
              <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
            </p>
          </li>
        </ul>
        <div class="no-rating" v-show="!food.ratings||!food.ratings.length">暂无评价</div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import BScroll from 'better-scroll'
import cartcontrol from '../cartcontrol/CartControl'
import 'animate.css'
import split from '../split/split'
import ratingselect from '../ratingselect/ratingselect'

const all = 2;
Vue.filter('time', function(val) {
  let oDate = new Date(val);
  let timeArr = [(oDate.getMonth() + 1), oDate.getDate(), oDate.getHours(), oDate.getMinutes()];
  timeArr.forEach((item, index) => {
    if (item < 10) {
      timeArr[index] = '0' + item;
    }
  });
  return oDate.getFullYear() + '-' + timeArr[0] + '-' + timeArr[1] + ' ' + timeArr[2] + ':' + timeArr[3];
});
export default {
  props: ['food'],
  data() {
    return {
      showFlag: false,
      selectType: all,
      onlyContent: false,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    }
  },
  methods: {
    show() {
      this.showFlag = true;
      this.selectType = all;
      this.onlyContent = false;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$els.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    hide() {
      this.showFlag = false;
    },
    addFirst(ev) {
      if (!ev._constructed) return;
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1);
        this.$dispatch('add', ev.target);
      }
    },
    filter(type, text) {
      if (this.onlyContent && !text) return false;
      if (this.selectType === all) {
        return true;
      } else {
        return type === this.selectType;
      }
    }
  },
  events: {
    ratingtype(type) {
      this.selectType = type;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    content(val) {
      this.onlyContent = val;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    }
  },
  components: {
    cartcontrol,
    ratingselect,
    split
  },
  transitions: {
    fade: {
      enterClass: 'fadeIn',
      leaveClass: 'zoomOut'
    }
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .food {
    position: fixed;
    left: 0;
    bottom: 96rem/$r;
    top: 0;
    right: 0;
    z-index: 60;
    background: white;
    .img-header {
      position: relative;
      width: 100%;
      height: 0;
      padding-top: 100%;
      img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
      .back {
        position: absolute;
        left: 0;
        top: 0;
        span {
          display: block;
          @extend %f-size40;
          color: #fff;
          padding: 20rem/$r;
          opacity: 0.8;
        }
      }
    }
    %padding {
      padding: 36rem/$r;
    }
    .content {
      @extend %padding;
      position: relative;
      .title {
        @extend %f-size28;
        @extend %f-weight700;
        line-height: 28rem/$r;
        @extend %color7-17-27;       
      }
      .detail {
        font-size: 0;
        margin: 16rem/$r 0 36rem/$r;
        span {
          @extend %f-size20;
          color: $color147-153-159;
          &:first-of-type {
            margin-right: 24rem/$r;
          }
        }
      }
      .price {
        font-size: 0;
        @extend %line-height48;
        .now {
          @extend %f-size28;
          @extend %f-weight700;
          vertical-align: middle;
          color: rgb(240, 20, 20);
          margin-right: 16rem/$r;
        }
        .old {
          @extend %f-size20;
          @extend %f-weight700;
          color: #93999f;
          text-decoration: line-through;
          vertical-align: middle;
        }
      }
      .cartcontrol-wrap {
        position: absolute;
        right: 16rem/$r;
        bottom: 18rem/$r;
      }
      .buy {
        position: absolute;
        bottom: 36rem/$r;
        right: 36rem/$r;
        height: 24rem/$r;
        padding: 12rem/$r 24rem/$r;
        background: $color0-160-220;
        color: #fff;
        @extend %f-size20;
        line-height: 24rem/$r;
        text-align: center;
        z-index: 10;
        border-radius: 24px;
      }
    }
    .info {
      @extend %padding;
      .title {
        @extend %f-size28;
        @extend %color7-17-27;
        margin-bottom: 12rem/$r;
      }
      .text {
        @extend %f-size24;
        @extend %f-weight;
        @extend %line-height48;
        color: #4d555d;
        margin: 0 16rem/$r;
      }
    }
    .rating {
      padding-top: 36rem/$r;
      .title {
        margin-left: 36rem/$r;
        @extend %f-size28;
        @extend %color7-17-27;
      }
    }
    .rating-content-wrap {
      padding: 0 36rem/$r;
      .rating-item {
        padding: 32rem/$r 0;
        position: relative;
        @include border-1px($color7-17-27-1);
        .user {
          position: absolute;
          top: 32rem/$r;
          right: 0;
          line-height: 24rem/$r;
          .avatar {
            width: 24rem/$r;
            height: 24rem/$r;
            border-radius: 50%;
            float: right;
          }
          .username {
            @extend %f-size20;
            color: $color147-153-159;
            vertical-align: middle;
            margin-right: 12rem/$r;
          }
        }
        .time {
          @extend %f-size20;
          margin-bottom: 12rem/$r;
          color: $color147-153-159;
          line-height: 24rem/$r;
        }
        .text {
          @extend %f-size24, %color7-17-27;
          line-height: 32rem/$r;
          span {
            margin-right: 8rem/$r;
            &.icon-thumb_up {
              color: $color0-160-220;
            }
            &.icon-thumb_down {
              color: $color147-153-159;
            }
          }
        }
      }
    }
    .no-rating {
      padding: 32rem/$r 0;
      @extend %f-size24;
      color: $color147-153-159;
    }
  }
</style>




