<template>
  <div class="shopcart">
    <div class="content">
      <div class="content-left">
        <div class="logo-wrap" @click="showCart">
          <div class="logo" :class="{highlight: totalCount > 0}">
            <span class="icon-shopping_cart" :class="{highlight: totalCount > 0}"></span>
          </div>
          <div class="num" v-show="totalPrice > 0">{{totalCount}}</div>
        </div>
        <div class="price" :class="{highlight: totalCount > 0}">￥{{totalPrice}}</div>
        <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
      </div>
      <div class="content-right" @click="alertPay">
        <div class="pay" :class="{highlight: totalPrice >= minPrice}">{{payDesc}}</div>
      </div>
      <div class="ball-container">
        <div class="ball" v-for="(index,ball) in balls" v-show="ball.show" transition="drop" :data-index="index"></div>
      </div>
      <div class="shopcart-list animated" v-show="listshow" transition="bounce">
        <div class="list-header">
          <h3 class="title">购物车</h3>
          <span class="empty" @click="empty">清空</span>
        </div>
        <div class="list-content" v-el:list>
          <ul>
            <li class="food" v-for="food in selectFoods">
              <span class="name">{{food.name}}</span>
              <div class="price">
                <span>￥{{food.price*food.count}}</span>
              </div>
              <div class="cart-wrap">
                <cartcontrol :food="food"></cartcontrol>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="alert animated" v-show="alert" transition="zoom">
      <div class="final-pay">
        <span>支付￥{{totalPrice}}元</span>
      </div>
      <div class="pay-btn">
        <div class="accept" @click="alertPay">支付</div>
        <div class="cancel" @click="alertPay">取消</div>
      </div>
    </div>
    <div class="alert-mask animated" transition="fade" v-show="alert"></div>
  </div>
  <div class="list-mask animated" v-show="listshow" transition="fade" @click="showCart"></div>
</template>
<script>
import cartcontrol from '../cartcontrol/CartControl'
import 'animate.css'
import BScroll from 'better-scroll'
export default {
  data() {
    return {
      balls: [
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        }
      ],
      fold: false,
      alert: false
    }
  },
  props: {
    selectFoods: {
      type: Array,
      default() {
        return [];
      }
    },
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    }
  },
  computed: {
    totalPrice() {
      let total = 0;
      this.selectFoods.forEach((food) => {
        total += food.price * food.count;
      });
      return total;
    },
    totalCount() {
      let count = 0;
      this.selectFoods.forEach((food) => {
        count += food.count;
      });
      return count;
    },
    payDesc() {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}起送`;
      } else if (this.totalPrice < this.minPrice) {
        let money = this.minPrice - this.totalPrice;
        return `还差￥${money}起送`;
      } else {
        return '去结算';
      }
    },
    listshow() {
      if (!this.totalCount) {
        this.fold = false;
        return false;
      } else {
        if (this.fold) {
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$els.list, {
                click: true
              });
            } else {
              this.scroll.refresh();
            }
          });
        }
        return this.fold;
      }
    }
  },
  methods: {
    drop(target) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = target;
          return;
        }
      }
    },
    showCart() {
      if (!this.totalCount) {
        return;
      } else {
        this.fold = !this.fold;
      }
    },
    empty() {
      this.selectFoods.forEach((food) => {
        food.count = 0;
      });
    },
    alertPay() {
      if (this.totalPrice < this.minPrice) return;
      this.alert = !this.alert;
    }
  },
  transitions: {
    drop: {
      beforeEnter(el) {
        let ball = this.balls[el.dataset.index];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let computed = window.getComputedStyle(el, null);
          let x = rect.left - window.parseFloat(computed.left);
          let y = -(window.innerHeight - rect.top - window.parseFloat(computed.bottom));
          el.style.webkitTransform = `translate3d(${x}px, ${y}px, 0)`;
          el.style.transform = `translate3d(${x}px, ${y}px, 0)`;
        }
      },
      enter(el) {
        /* eslint-disable no-unused-vars */
        let tmp = el.offsetLeft; // 浏览器重绘
        el.style.webkitTransform = 'translate3d(0, 0, 0)';
        el.style.transform = 'translate3d(0, 0, 0)';
      },
      afterEnter(el) {
        let i = el.dataset.index;
        this.balls[i].show = false;
        el.style.display = 'none';
      }
    },
    bounce: {
      enterClass: 'bounceInUp',
      leaveClass: 'bounceOutDown'
    },
    fade: {
      enterClass: 'fadeIn',
      leaveClass: 'fadeOut'
    },
    zoom: {
      enterClass: 'zoomInLeft',
      leaveClass: 'zoomOutRight'
    }
  },
  components: {
    cartcontrol
  }
}
</script>

<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  %color-white {
    color: #fff;
  }
  %mask {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(7, 17, 27, 0.6);
    -webkit-backdrop-filter: blur(10px);
    z-index: 99;
  }
  .shopcart {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 96rem/$r;
    z-index: 100;
    .content {
      display: flex;
      color: rgba(255, 255, 255, 0.4);
      .content-left {
        flex: 1;
        background: #141d27;
        font-size: 0;
        .logo-wrap {
          display: inline-block;
          position: relative;
          width: 88rem/$r;
          height: 88rem/$r;
          position: relative;
          top: -20rem/$r;
          background: #141d27;
          padding: 12rem/$r;
          margin: 0 24rem/$r;
          border-radius: 50%;
          vertical-align: top;
          .logo {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background: #2b343c;
            text-align: center;
            .icon-shopping_cart {
              color: #80858a;
              @extend %f-size48;
              line-height: 88rem/$r;
              &.highlight {
                @extend %color-white;
              }
            }
            &.highlight {
              background: #00a0dc;
            }
          }
          .num {
            position: absolute;
            width: 48rem/$r;
            height: 32rem/$r;
            line-height: 32rem/$r;
            font-size: 18rem/$r;
            @extend %f-weight700;
            color: white;
            text-align: center;
            background: rgb(240, 20, 20);
            box-shadow: 0 4rem/$r 8rem/$r 0 rgba(0, 0, 0, 0.4);
            border-radius: 16rem/$r;
            top: 0;
            right: 0;
          }
        }
        .price {
          display: inline-block;
          font-size: 32rem/$r;
          @extend %f-weight700;
          padding: 8rem/$r 24rem/$r 8rem/$r 0;
          margin: 24rem/$r 0;
          line-height: 32rem/$r;
          border-right: 1px solid rgba(255, 255, 255, 0.1);
          vertical-align: middle;
          &.highlight {
            @extend %color-white;
          }
        }
        .desc {
          display: inline-block;
          font-size: 20rem/$r;
          margin-left: 24rem/$r;
          vertical-align: middle;
        }
      }
      .content-right {
        flex: 0 0 210rem/$r;
        width: 210rem/$r;
        .pay {
          height: 96rem/$r;
          width: 210rem/$r;
          background: #2b333b;
          line-height: 96rem/$r;
          font: {
            size: 24rem/$r;
            weight: bold;
          }
          text-align: center;
          &.highlight {
            background: #00b43c;
            @extend %color-white;
          }
        }
      }
      .ball {
        width: 32rem/$r;
        height: 32rem/$r;
        background: $color0-160-220;
        border-radius: 50%;
        position: fixed;
        left: 64rem/$r;
        bottom: 44rem/$r;
        z-index: 200;
        &.drop-transition {
          transition: 0.5s cubic-bezier(0.49, -0.29, 0.75, 0.41);
        }
      }
      .shopcart-list {
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 96rem/$r;
        z-index: -1;
        .list-header {
          height: 80rem/$r;
          line-height: 80rem/$r;
          padding: 0 36rem/$r;
          background: #f3f5f7;
          border-bottom: 2px solid $color7-17-27-1;
          font-size: 0;
          .title {
            @extend %f-size28;
            @extend %f-weight;
            @extend %color7-17-27;
            display: inline-block;
          }
          .empty {
            @extend %f-size24;
            color: $color0-160-220;
            float: right;
          }
        }
        .list-content {
          background: white;
          padding: 0 36rem/$r;
          max-height: 435rem/$r;
          overflow: hidden;
          .food {
            height: 48rem/$r;
            padding: 24rem/$r 0;
            @extend %line-height48;
            @include border-1px($color7-17-27-1);
            position: relative;
            .name {
              @extend %f-size28;
              @extend %color7-17-27;
              vertical-align: middle;
            }
            .price {
              vertical-align: middle;
              position: absolute;
              right: 170rem/$r;
              bottom: 24rem/$r;
              span {
                @extend %f-size28;
                font-weight: bold;
                color: rgb(240, 20, 20);
              }
            }
            .cart-wrap {
              position: absolute;
              right: -16rem/$r;
              top: 6rem/$r;
            }
          }
        }
      }
    }
    .alert {
      width: 594rem/$r;
      height: 367rem/$r;
      text-align: center;
      background: rgba(255, 255, 255, 0.9);
      border: 1px solid $color7-17-27-1;
      border-radius: 16rem/$r;
      position: fixed;
      left: 78rem/$r;
      top: 442rem/$r;
      z-index: 1000;
      .final-pay {
        margin-top: 70rem/$r;
        @extend %f-size48;
        color: rgba(7, 17, 27, 0.5);
      }
      .pay-btn {
        position: absolute;
        bottom: 90rem/$r;
        left: 0;
        width: 100%;
        font-size: 0;
        %btn {
          width: 100rem/$r;
          height: 60rem/$r;
          border-radius: 16rem/$r;
          position: absolute;
          bottom: 0;
          text-align: center;
          @extend %f-size28;
          line-height: 60rem/$r;
          color: #f3f5f7;
        }
        @mixin btn($color, $position) {
          background: $color;
          #{$position}: 150rem/$r;
        }
        .accept {
          @include btn(rgba(0, 160, 220, 0.8), left);
          @extend %btn;
        }
        .cancel {
          @include btn(rgba(red, 0.7), right);
          @extend %btn;
        }
      }
    }
  .alert-mask {
    @extend %mask;
  }
  }
  .list-mask {
    @extend %mask;
  }
</style>



