<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in classList" :class="itemClass" class="star-item" track-by="$index"></span>
  </div>
</template>
<script>
const length = 5;
const clsOn = 'on';
const clsHalf = 'half';
const clsOff = 'off';

export default {
  props: ['size', 'score'],
  computed: {
    starType() {
      return 'star-' + this.size;
    },
    classList() {
      let result = [];
      let score = Math.floor(this.score * 2) / 2;
      let hasHalf = score % 1 !== 0;
      let integer = Math.floor(score);
      for (var i = 0; i < integer; i++) {
        result.push(clsOn);
      }
      if (hasHalf) {
        result.push(clsHalf);
      }
      while (result.length !== length) {
        result.push(clsOff);
      }
      return result;
    }
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .star {
    font-size: 0;
    .star-item {
      display: inline-block;
      background-repeat: no-repeat;
    }
  }
  @mixin star-type($type,$width,$height,$margin) {
    .star-item {
      width: $width;
      height: $height;
      background-size: $width $height;
      margin-right: $margin;
      &:last-child {
        margin-right: 0;
      }
    }
    & .on {
      @include bg(star#{$type}_on);
    }
    & .half {
      @include bg(star#{$type}_half);
    }
    & .off {
      @include bg(star#{$type}_off);
    }
  }
  .star-48 {
    @include star-type(48,44rem/$r,38rem/$r,44rem/$r);
  }
  .star-36 {
    @include star-type(36,30rem/$r,29rem/$r,12rem/$r);
  }
  .star-24 {
    @include star-type(24,20rem/$r,19rem/$r,6rem/$r);
  }
</style>




