<template>
  <div class="goods">
    <div class="menu-wrap" v-el:menu-wrap>
      <ul>
        <li v-for="item in goods" class="menu-item" :class="{current: currentIndex === $index}" @click="selectMenu($index,$event)">
          <span class="text">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrap" v-el:foods-wrap>
      <ul>
        <li class="food-list" v-for="item in goods">
          <h2 class="title">{{item.name}}</h2>
          <ul>
            <li class="food-item" v-for="food in item.foods" @click="select(food, $event)">
              <div class="icon">
                <img :src="food.icon">
              </div>
              <div class="content">
                <h3 class="name">{{food.name}}</h3>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span>月售{{food.sellCount}}</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="control-wrap">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart v-ref:shopcart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
  </div>
  <food :food="selectedFood" v-ref:food class="animated" transition="zoom"></food>
</template>
<script>
import BScroll from 'better-scroll'
import shopcart from '../shopCart/ShopCart'
import cartcontrol from '../cartcontrol/CartControl'
import food from '../../components/food/food'
import 'animate.css'
export default {
  data() {
    return {
      goods: [],
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      heightList: [],
      scrollY: 0,
      selectedFood: {}
    };
  },
  ready() {
    this.$http.get('/api/goods').then((res) => {
      res = res.data;
      if (res.errno === 0) {
        this.goods = res.data;
        this.$nextTick(() => {
          this.initScroll();
          this.calculate();
        });
      }
    }, (err) => {
      console.log('err' + err);
    });
  },
  methods: {
    initScroll() {
      this.menuWrap = new BScroll(this.$els.menuWrap, {
        click: true
      });
      this.foodsWrap = new BScroll(this.$els.foodsWrap, {
        click: true,
        probeType: 3
      });
      this.foodsWrap.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    calculate() {
      let foodList = this.$els.foodsWrap.getElementsByClassName('food-list');
      let height = 0;
      this.heightList.push(height);
      for (let i = 0; i < foodList.length; i++) {
        height += foodList[i].offsetHeight;
        this.heightList.push(height);
      }
    },
    selectMenu(index, event) {
      if (!event._constructed) {
        return;
      }
      let foodList = this.$els.foodsWrap.getElementsByClassName('food-list');
      let el = foodList[index];
      this.foodsWrap.scrollToElement(el, 300);
    },
    drop(target) {
      this.$refs.shopcart.drop(target);
    },
    select(food, ev) {
      if (!ev._constructed) {
        return;
      }
      this.selectedFood = food;
      this.$refs.food.show();
    }
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.heightList.length; i++) {
        let height = this.heightList[i];
        let nextHeight = this.heightList[i + 1];
        if (!nextHeight || (this.scrollY >= height && this.scrollY < nextHeight)) {
          return i;
        }
      }
      return 0;
    },
    selectFoods() {
      let select = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count) {
            select.push(food);
          }
        })
      });
      return select;
    }
  },
  props: ['seller'],
  components: {
    shopcart,
    cartcontrol,
    food
  },
  events: {
    add(target) {
      this.drop(target);
    }
  },
  transitions: {
    zoom: {
      enterClass: 'lightSpeedIn',
      leaveClass: 'zoomOut'
    }
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .goods {
    display: flex;
    position: absolute;
    top: 346rem/$r;
    bottom: 96rem/$r;
    width: 100%;
    overflow: hidden;
    .menu-wrap {
      flex: 0 0 112rem/$r;
      width: 112rem/$r;
      padding: 0 24rem/$r;
      background: #f3f5f7;
      height: 100%;
    }
    .foods-wrap {
      flex: 1;
    }
  }
  .menu-item {
    width: 112rem/$r;
    height: 108rem/$r;
    line-height: 28rem/$r;
    display: table;
    font-size: 0;
    &.current {
      line-height: 28rem/$r;
      background: white;
      padding: 0 24rem/$r;
      position: relative;
      top: -2rem/$r;
      left: -24rem/$r;
      &:after {
        border: none;
      }
      .text {
        @extend %f-weight700;
      }
    }
    @include border-1px($color7-17-27-1);
    .text {
      display: table-cell;
      width: 112rem/$r;
      line-height: 28rem/$r;
      vertical-align: middle;
      @extend %f-weight;
      @extend %f-size24;
    }
    .icon {
      width: 24rem/$r;
      height: 24rem/$r;
      display: inline-block;
      margin-right: 4rem/$r;
      vertical-align: top;
      background: {
        repeat: no-repeat;
        size: 24rem/$r 24rem/$r; 
      }
      @include icon(_3);
    }
  }
  .food-list {
    .title {
      font: {
        size: 24rem/$r;
        weight: bold;
      }
      color: $color147-153-159;
      height: 52rem/$r;
      line-height: 52rem/$r;
      padding-left: 28rem/$r;
      border-left: 2px solid #d9dde1;
      background: #f3f5f7;
    }
    ul {
      padding: 0 36rem/$r;
    }
    .food-item {
      padding: 36rem/$r 0;
      display: flex;
      @include border-1px($color7-17-27-1);
      &:last-child:after {
        display: none;
      }
      .icon {
        flex: 0 0 114rem/$r;
        margin-right: 20rem/$r;
        img {
          width: 114rem/$r;
          height: 114rem/$r;
          border-radius: 2px;
        }
      }
      .content {
        flex: 1;
        h3 {
          @extend %f-size28;
          @extend %color7-17-27;
          line-height: 28rem/$r;
          margin-top: 4rem/$r;
        }
        %public {
          line-height: 26rem/$r;
          color: #93999f;
          font-size: 20rem/$r;
        }
        .desc {
          @extend %public;
          margin: 16rem/$r 0;   
        }
        .extra {
          font-size: 0;
          span {
            @extend %public;
            &:first-of-type {
              margin-right: 24rem/$r;
            }
          }
        }
        .price {
          @extend %line-height48;
          @extend %f-weight700;
          .now {
            @extend %f-size28;
            color: #f01414;
            margin-right: 16rem/$r;
          }
          .old {
            @extend %f-size20;
            color: #93999f;
            text-decoration: line-through;
          }
        }
        .control-wrap {
          position: absolute;
          right: -20rem/$r;
          bottom: 20rem/$r;
        }
      }
    }
  }
</style>



