<template>
  <div class="ratings" v-el:ratings>
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <div class="score">{{seller.score}}</div>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrap">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrap">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-time">
            <span class="title">送达时间</span>
            <span class="time">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect :ratings="ratings" :select-type="selectType" :only-content="onlyContent"></ratingselect>
      <div class="ratings-list">
        <ul>
          <li v-for="rating in ratings" class="rating-item" v-show="filter(rating.rateType, rating.text)">
            <div class="avatar">
              <img :src="rating.avatar">
            </div>
            <div class="content">
              <h3 class="username">{{rating.username}}</h3>
              <div class="star-wrap">
                <star :size="24" :score="rating.score"></star>
                <span class="delivery-time" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend">
                <span class="icon-thumb_up" v-show="!rating.rateType"></span>
                <span v-for="item in rating.recommend" class="item">{{item}}</span>
              </div>
              <div class="time">{{rating.rateTime | time}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import star from '../star/star'
import split from '../split/split'
import ratingselect from '../ratingselect/ratingselect'
import BScroll from 'better-scroll'

const all = 2;
export default {
  data() {
    return {
      showFlag: false,
      selectType: all,
      onlyContent: true,
      ratings: []
    }
  },
  props: ['seller'],
  components: {
    star,
    split,
    ratingselect
  },
  methods: {
    filter(type, text) {
      if (this.onlyContent && !text) return false;
      if (this.selectType === all) {
        return true;
      } else {
        return type === this.selectType;
      }
    }
  },
  ready() {
    this.$http.get('/api/ratings').then((res) => {
      res = res.data;
      if (res.errno === 0) {
        this.ratings = res.data;
        this.$nextTick(() => {
          this.scroll = new BScroll(this.$els.ratings, {
            click: true
          });
        });
      }
    });
  },
  events: {
    ratingtype(type) {
      this.selectType = type;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    content(val) {
      this.onlyContent = val;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    }
  }
}
</script>

<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .ratings {
    position: absolute;
    top: 346rem/$r;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    .overview {
      display: flex;
      padding: 36rem/$r 0;
      .overview-left {
        flex: 0 0 275rem/$r;
        width: 275rem/$r;
        text-align: center;
        border-right: 1px solid $color7-17-27-1;
        .score {
          @extend %f-size48;
          line-height: 56rem/$r;
          color: rgb(255, 153, 0);
        }
        .title {
          @extend %f-size24, %color7-17-27;
          line-height: 24rem/$r;
          margin: 12rem/$r 0 16rem/$r;
        }
        .rank {
          @extend %f-size20;
          color: $color147-153-159;
          line-height: 20rem/$r;
          margin-bottom: 12rem/$r;
        }
      }
      .overview-right {
        flex: 1;
        padding: 0 48rem/$r;
        .score-wrap {
          font-size: 0;
          margin-bottom: 16rem/$r;
          .title {
            @extend %f-size24, %color7-17-27;
            line-height: 36rem/$r;
          }
          .star {
            display: inline-block;
            margin: 0 24rem/$r;
            vertical-align: top;
          }
          .score {
            @extend %f-size24;
            line-height: 36rem/$r;
            color: rgb(255, 153, 0);
          }
        }
        .delivery-time {
          font-size: 0;
          span {
            @extend %f-size24;
            line-height: 36rem/$r;
            &.title {
              @extend %color7-17-27;
              margin-right: 24rem/$r;
            }
            &.time {
              color: $color147-153-159;
            }
          }
        }
      }
    }
    .ratings-list {
      padding: 0 36rem/$r;
      .rating-item {
        padding: 36rem/$r 0 28rem/$r;
        position: relative;
        @include border-1px($color7-17-27-1);
        display: flex;
        .avatar {
          flex: 0 0 56rem/$r;
          width: 56rem/$r;
          margin-right: 24rem/$r;
          img {
            width: 56rem/$r;
            height: 56rem/$r;
            border-radius: 50%;
          }
        }
        .content {
          flex: 1;
          .username {
            @extend %f-size20 ,%color7-17-27;
            line-height: 24rem/$r;
          }
          .star-wrap {
            font-size: 0;
            margin: 8rem/$r 0 12rem/$r;
            .star {
              display: inline-block;
              margin-right: 12rem/$r;
            }
            .delivery-time {
              @extend %f-size20, %f-weight;
              line-height: 24rem/$r;
              color: $color147-153-159;
            }
          }
          
          .text {
            @extend %f-size24, %color7-17-27;
            line-height: 36rem/$r;
            margin-bottom: 16rem/$r;
          }
          .time {
            position: absolute;
            right: 0;
            top: 36rem/$r;
            @extend %f-size20, %f-weight;
            line-height: 24rem/$r;
            color: $color147-153-159;
          }
          .recommend {
            font-size: 0;
            margin-top: 12rem/$r;
            .icon-thumb_up, .item {
              line-height: 32rem/$r;
              vertical-align: middle;
              display: inline-block;
              margin-bottom: 8rem/$r;
            }
            .icon-thumb_up {
              @extend %f-size24;
              color: $color0-160-220;
            }
            .item {
              margin-left: 16rem/$r;
              padding: 0 12rem/$r;
              border: 1px solid $color7-17-27-1;
              border-radius: 2px;
              font-size: 18rem/$r;
              color: $color147-153-159;
            }
          }
        }
      }
    }
  }
</style>



