<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span class="block positive" :class="{active:selectType===2}" @click="select(2,$event)">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
      <span class="block positive" :class="{active:selectType===0}" @click="select(0,$event)">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
      <span class="block negative" :class="{active:selectType===1}" @click="select(1,$event)">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
    </div>
    <div class="switch">
      <span class="icon-check_circle" :class="{on:onlyContent}" @click="toggleContent"></span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>
<script>
const positive = 0;
const negative = 1;
const all = 2;
export default {
  props: {
    ratings: {
      type: Array,
      default() {
        return [];
      }
    },
    selectType: {
      type: Number,
      default() {
        return all;
      }
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default() {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        };
      }
    }
  },
  methods: {
    select(type, ev) {
      if (!ev._constructed) return;
      this.selectType = type;
      this.$dispatch('ratingtype', type);
    },
    toggleContent(ev) {
      if (!ev._constructed) return;
      this.onlyContent = !this.onlyContent;
      this.$dispatch('content', this.onlyContent);
    }
  },
  computed: {
    positives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === positive;
      });
    },
    negatives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === negative;
      });
    }
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .ratingselect {
    .rating-type {
      padding: 36rem/$r 0;
      margin: 0 36rem/$r;
      font-size: 0;
      @include border-1px($color7-17-27-1);
      .block {
        display: inline-block;
        padding: 16rem/$r 24rem/$r;
        margin-right: 16rem/$r;
        @extend %f-size24;
        line-height: 32rem/$r;
        color: rgb(77, 85, 93);
        border-radius: 2px;
        .count {
          margin-left: 8rem/$r;
          font-size: 14rem/$r;
        }
        &.active {
          color: #fff;
        }
      }
      .positive {
        background: rgba(0, 160, 220, 0.2);
        &.active {
          background: $color0-160-220;
        }
      }
      .negative {
        background: rgba(77, 85, 93, 0.2);
        &.active {
          background: rgb(77, 85, 93);
        }
      }
    }
    .switch {
      padding: 24rem/$r 36rem/$r;
      color: $color147-153-159;
      border-bottom: 2px solid $color7-17-27-1;
      font-size: 0;
      @extend %line-height48;
      .icon-check_circle {
        @extend %f-size48;
        vertical-align: middle;
        &.on {
          color: #00c850;
        }
      }
      .text {
        @extend %f-size24;
        vertical-align: middle;
        margin-left: 8rem/$r;
      }
    }
  }
</style>


