<template>
  <div class="seller" v-el:seller>
    <div class="seller-content">
      <div class="overview">
        <div class="overview-top">
          <h1 class="title">{{seller.name}}</h1>
          <div class="desc">
            <star :size="36" :score="seller.score"></star>
            <span class="rating-count">({{seller.ratingCount}})</span>
            <span class="sell-count">月售{{seller.sellCount}}单</span>
          </div>
          <div class="favorite">
            <span class="icon-favorite" :class="{active:favorite}" @click="collect"></span>
            <span class="text">{{favorited}}</span>
          </div>
        </div>
        <div class="overview-bottom">
          <div class="delivery">
            <div class="title">起送价</div>
            <div class="desc"><span>{{seller.minPrice}}</span>元</div>
          </div>
          <div class="delivery">
            <div class="title">商家配送</div>
            <div class="desc"><span>{{seller.deliveryPrice}}</span>元</div>
          </div>
          <div class="delivery">
            <div class="title">平均配送时间</div>
            <div class="desc"><span>{{seller.deliveryTime}}</span>分钟</div>
          </div>
        </div>
      </div>
      <split></split>
      <div class="bulletins">
        <h2 class="bulletin-title">公告与活动</h2>
        <p class="bulletin-text">{{seller.bulletin}}</p>
        <ul v-if="seller.supports">
          <li class="support-item" v-for="item in seller.supports">
            <span class="icon" :class="classMap[item.type]"></span>
            <span class="info">{{item.description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="seller-picture" v-if="seller.pics&&seller.pics.length">
        <h2 class="title">商家实景图</h2>
        <div class="pics" v-el:wrap>
          <ul v-el:list>
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic">
            </li>
          </ul>
        </div>
      </div>
      <split v-if="seller.pics&&seller.pics.length"></split>
      <div class="seller-info">
        <h2 class="title">商家信息</h2>
        <ul>
          <li class="info-item" v-for="info in seller.infos">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import star from '../star/star'
import split from '../split/split'
import BScroll from 'better-scroll'
import {storeSet, storeGet} from '../../commom/js/store.js'
export default {
  data() {
    return {
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      favorite: storeGet(this.seller.id, 'favorite')
    };
  },
  computed: {
    favorited() {
      if (this.favorite) {
        return '已收藏'
      } else {
        return '收藏'
      }
    }
  },
  watch: {
    seller() {
      this.initScroll();
      this.listWidth();
    }
  },
  ready() {
    this.initScroll();
    this.listWidth();
  },
  methods: {
    initScroll() {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$els.seller, {
          click: true
        });
      } else {
        this.scroll.refresh();
      }
    },
    listWidth() {
      if (this.seller.pics && this.seller.pics.length) {
        let oLi = document.getElementsByClassName('pic-item')[0];
        let margin = window.parseFloat(window.getComputedStyle(oLi, null).marginRight);
        this.$els.list.style.width = (oLi.offsetWidth + margin) * this.seller.pics.length - margin + 'px';
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$els.wrap, {
              scrollX: true
            });
          } else {
            this.picScroll.refresh();
          }
        });
      }
    },
    collect() {
      this.favorite = !this.favorite;
      storeSet(this.seller.id, 'favorite', this.favorite);
    }
  },
  props: ['seller'],
  components: {
    star,
    split
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .seller {
    position: absolute;
    top: 346rem/$r;
    bottom: 0;
    width: 100%;
    overflow: hidden;
  }
  .overview {
    padding: 36rem/$r;
    .overview-top {
      @include border-1px($color7-17-27-1);
      padding-bottom: 36rem/$r;
      position: relative;
      .title {
        @extend %f-size28, %color7-17-27;
        line-height: 28rem/$r;
      }
      .desc {
        font-size: 0;
        margin-top: 16rem/$r;
        .star, .rating-count, .sell-count {
          display: inline-block;
          vertical-align: top;
        }
        .star {
          margin-right: 16rem/$r;
        }
        .sell-count {
          @extend %f-size20;
          color: rgb(77, 85, 93);
          line-height: 36rem/$r;
        }
        .rating-count {
          @extend .sell-count;
          margin-right: 24rem/$r;
        }
      }
      .favorite {
        position: absolute;
        right: 0;
        top: 0;
        text-align: center;
        width: 60rem/$r;
        .icon-favorite, .text {
          display: block;
        }
        .icon-favorite {
          @extend %f-size48, %line-height48;
          color: rgba(0, 0, 0, 0.2);
          margin-bottom: 8rem/$r;
          &.active {
            color: rgb(240, 20, 20);
          }
        }
        .text {
          @extend %f-size20;
          color: rgb(77, 85, 93);
          line-height: 20rem/$r;
        }
      }
    }
    .overview-bottom {
      display: flex;
      padding-top: 36rem/$r;
      .delivery {
        flex: 1;
        text-align: center;
        border-right: 1px solid $color7-17-27-1;
        &:last-of-type {
          border-right: none;
        }
        .title {
          @extend %f-size20;
          color: $color147-153-159;
          line-height: 20rem/$r;
          margin-bottom: 8rem/$r;
        }
        .desc {
          @extend %f-size20, %color7-17-27, %f-weight;
          span {
            @extend %f-size48, %line-height48;
          }
        }
      }
    }
  }
  .bulletins {
    padding: 36rem/$r 36rem/$r 0 36rem/$r;
    .bulletin-title {
      @extend %f-size28, %color7-17-27;
      line-height: 28rem/$r;
      margin-bottom: 16rem/$r;
    }
    .bulletin-text {
      @extend %f-size24, %f-weight, %line-height48;
      color: rgb(240, 20, 20);
      padding: 0 24rem/$r 36rem/$r;
    }
    .support-item {
      @include border-1px-before($color7-17-27-1);
      padding: 32rem/$r 24rem/$r;
      line-height: 32rem/$r;
      .info {
        @extend %f-size24, %f-weight, %color7-17-27;
        vertical-align: middle;
      }
      .icon {
        display: inline-block;
        width: 32rem/$r;
        height: 32rem/$r;
        margin-right: 12rem/$r;
        vertical-align: middle;
        background: {
          repeat: no-repeat;
          size: 32rem/$r 32rem/$r;
        }
        @include icon(_4);
      }
    }
  }
  .seller-picture {
    padding: 36rem/$r;
    .title {
      @extend %f-size28, %color7-17-27;
      line-height: 28rem/$r;
      margin-bottom: 24rem/$r;
    }
    .pics {
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      .pic-item {
        width: 240rem/$r;
        height: 180rem/$r;
        margin-right: 12rem/$r;
        display: inline-block;
        &:last-of-type {
          margin-right: 0;
        }
        img {
          width: 240rem/$r;
          height: 180rem/$r;
        }
      }
    }
  }
  .seller-info .title,.seller-picture .title {
    @extend %f-size28, %color7-17-27;
    line-height: 28rem/$r;
    margin-bottom: 24rem/$r;
  }
  .seller-info {
    padding: 36rem/$r 36rem/$r 0 36rem/$r;
    .info-item {
      @include border-1px-before($color7-17-27-1);
      @extend %f-size24, %f-weight, %color7-17-27;
      line-height: 32rem/$r;
      padding: 32rem/$r 24rem/$r;
    }
  }
</style>



