<template>
    <div class="heade">
        <div class="content">
            <div class="avatar">
                <img :src="seller.avatar">
            </div>
            <div class="content-info">
                <div class="title">
                    <span class="brand"></span>
                    <h3>{{seller.name}}</h3>
                </div>
                <div class="description">
                    <span>{{seller.description}} / {{seller.deliveryTime}}分钟送达</span>
                </div>
                <div class="support" v-if="seller.supports">
                    <span class="icon" :class="classMap[seller.supports[0].type]"></span>
                    <span>{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div class="support-count" v-if="seller.supports" @click="showDetail">
                <span class="count">{{seller.supports.length}}个</span>
                <span class="icon-keyboard_arrow_right"></span>
            </div>
        </div>
        <div class="bulletin" @click="showDetail">
            <span class="bulletin-title"></span>
            <span class="bulletin-info">{{seller.bulletin}}</span>
            <span class="icon-keyboard_arrow_right"></span>
        </div>
        <div>
            <img class="background" :src="seller.avatar">
        </div>
        <div class="detail" v-show="detail" transition="fade">
            <div class="detail-wrap clear">
                <div class="detail-content">
                  <h1 class="name">{{seller.name}}</h1>
                  <div class="star-wrap">
                    <star :size="48" :score="seller.score"></star>
                  </div>
                  <div class="title">
                    <div class="line"></div>
                    <div class="text">优惠信息</div>
                    <div class="line"></div>
                  </div>
                  <ul class="supports" v-if="seller.supports">
                      <li class="support-item" v-for="item in seller.supports">
                        <span class="support-item-icon" :class="classMap[item.type]"></span>
                        <span class="support-item-text">{{item.description}}</span>
                      </li>
                  </ul>
                  <div class="title">
                    <div class="line"></div>
                    <div class="text">商家公告</div>
                    <div class="line"></div>
                  </div>
                  <div class="bulletin-detail">
                      <p>{{seller.bulletin}}</p>
                  </div>
                </div>
            </div>
            <div class="detail-close" @click="hideDetail">
                <span class="icon-close"></span>
            </div>
        </div>
    </div>
</template>
<script>
import star from '../star/star'
export default {
  data() {
    return {
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      detail: false
    };
  },
  methods: {
    showDetail() {
      this.detail = true;
    },
    hideDetail() {
      this.detail = false;
    }
  },
  props: ['seller'],
  components: {
    star
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  @import '../../commom/sass/mixin.scss';
  .heade {
      color: #fff;
      background: rgba(7,17,27,0.5);
      position: relative;
      overflow: hidden;
      .content {
          font-size: 0;
          padding: 48rem/$r 24rem/$r 35rem/$r 48rem/$r;
          overflow: hidden;
          position: relative;
      }
  }
  .avatar {
      float: left;
      img {
          border-radius: 4px;
      }
  }
  .title {
        .brand {
            width: 60rem/$r;
            height: 36rem/$r;
            display: inline-block;
            margin: 4rem/$r 12rem/$r 16rem/$r 0;
            @include bg(brand);
            background: {
                size: 60rem/$r 36rem/$r;
                repeat: no-repeat;
            }
            vertical-align: top;
        }
        h3 {
            font: {
                size: 32rem/$r;
                weight: bold;
            }
            line-height: 36rem/$r;
            display: inline-block;
        }
    }
  .content-info {
      float: left;
      margin-left: 32rem/$r;
        .description {
            margin-bottom: 20rem/$r;
            span {
                @extend %f-size24;
                @extend %f-weight;
              }
      }
    }
  .support {
      span {
          @extend %f-size20, %f-weight;
          @include icon(_1);
      }
      .icon {
          width: 24rem/$r;
          height: 24rem/$r;
          display: inline-block;
          vertical-align: top;
          margin: 0 8rem/$r 4rem/$r 0;
          background: {
              size: 24rem/$r 24rem/$r;
              repeat: no-repeat;
          }
      }
  }
  .avatar img {
      width: 128rem/$r;
      height: 128rem/$r;
  } 
  .support-count {
      width: 86rem/$r;
      height: 48rem/$r;
      background: rgba(0,0,0,0.2);
      position: absolute;
      right: 24rem/$r;
      bottom: 30rem/$r;
      border-radius: 24rem/$r;
      text-align: center;
      @extend %line-height48;
      .count {
          @extend %f-size20, %f-weight;
          vertical-align: middle;
          margin-right: 4rem/$r;
      }
      .icon-keyboard_arrow_right {
          @extend %f-size20, %f-weight;
          vertical-align: middle;
      }
  }
  .bulletin {
      height: 56rem/$r;
      line-height: 56rem/$r;
      padding: 0 54rem/$r 0 24rem/$r;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      background: rgba(7,17,27,0.2);
      @extend %f-size20, %f-weight;
      position: relative;
      .bulletin-title {
          float: left;
          width : 44rem/$r;
          height: 24rem/$r;
          background: {
              size: 44rem/$r 24rem/$r;
              repeat: no-repeat;
          }
          @include bg(bulletin);
          margin: 16rem/$r 8rem/$r 0 0;
      }
      .icon-keyboard_arrow_right {
          position: absolute;
          right: 27rem/$r;
          bottom: 18rem/$r;
      }
  }
  .heade {
      .background {
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
          z-index: -1;
          filter: blur(10px);
      }
      .detail {
          position: fixed;
          overflow: auto;
          z-index: 1000;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          -webkit-backdrop-filter: blur(10px);
          transition: 0.5s;
          &.fade-transition {
              opacity: 1;
              background: rgba(7,17,27,0.8);
          }
          &.fade-enter,&.fade-leave {
              opacity: 0;
              background: rgba(7,17,27,0);
          }
      }
  }
  .detail-wrap {
      min-height: 100%;
      width: 100%;
      .detail-content {
          margin-top: 128rem/$r;
          padding: 0 72rem/$r 128rem/$r;
          .name {
              font-size: 32rem/$r;
              @extend %f-weight700;
              line-height: 32rem/$r;
              text-align: center;
          }
          .star-wrap {
              padding: 4rem/$r 0;
              margin: {
                  top: 36rem/$r;
                  bottom: 56rem/$r;
              }
              text-align: center;
          }
      }
  }
  .detail-content .title {
      display: flex;
      margin-bottom: 48rem/$r;
      .line {
          flex: 1;
          border-top: 2rem/$r solid rgba(255,255,255,0.2);
          position: relative;
          top: 12rem/$r;
      }
      .text {
          @extend %f-size28;
          @extend %f-weight700;
          margin: 0 24rem/$r;
      }

  }
  .supports {
      margin-bottom: 56rem/$r;
      .support-item {
          height: 32rem/$r;
          line-height: 32rem/$r;
          font-size: 0;
          margin: 0 24rem/$r 24rem/$r 24rem/$r;
          &:last-child {
              margin-bottom: 0;
          }
          .support-item-text {
            font: {
            size: 24rem/$r;
            weight: 200;
            }
            vertical-align: top;
          }
          .support-item-icon {
              display: inline-block;
              width: 32rem/$r;
              height: 32rem/$r;
              margin-right: 12rem/$r;
              background: {
                  size: 32rem/$r 32rem/$r;
                  repeat: no-repeat;
              }
              @include icon(_2);
          }
      }
  }
  .bulletin-detail {
      margin: 0 24rem/$r;
      p {
          @extend %f-weight;
          @extend %f-size24;
          @extend %line-height48;
      }
  }
  .detail-close {
      position: relative;
      width: 64rem/$r;
      height: 64rem/$r;
      margin: -128rem/$r auto 0 auto;
      clear: both;
      font-size: 64rem/$r;
      color: rgba(255, 255, 255, 0.5);
  }
</style>




