<template>
  <div class="cart-control">
    <div class="decrease icon-remove_circle_outline" v-show="food.count>0" @click="removeFood" transition="move"></div>
    <div class="conut" v-show="food.count>0" transition="fade">{{food.count}}</div>
    <div class="add icon-add_circle" @click="addFood"></div>
  </div>
</template>
<script>
import Vue from 'vue'
export default {
  props: ['food'],
  methods: {
    addFood(event) {
      if (!event._constructed) return;
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1);
      } else {
        this.food.count++;
      }
      this.$dispatch('add', event.target);
      event.stopPropagation();
    },
    removeFood(event) {
      if (!event._constructed) return;
      if (this.food.count) {
        this.food.count--;
      }
      event.stopPropagation();
    }
  }
}
</script>
<style lang="scss">
  @import '../../commom/sass/variable.scss';
  .cart-control {
    font-size: 0;
    %display {
      display: inline-block;
      @extend %line-height48;
      vertical-align: middle;
    }
    %font {
      @extend %f-size48;
      color: $color0-160-220;
      padding: 18rem/$r 0;
    }
    .decrease {
      @extend %display, %font;
      &.move-transition {
        transition: 0.5s;
        transform: translateX(0) rotate(0);
        opacity: 1;
      }
      &.move-enter,&.move-leave {
        opacity: 0;
        transform: translateX(48rem/$r) rotate(360deg);
      }
    }
    .conut {
      @extend %display, %f-size20;
      text-align: center;
      width: 48rem/$r;
      color: #93999f;
      &.fade-transition {
        transition: 0.5s;
        opacity: 1;
      }
      &.fade-enter,&.fade-leave {
        opacity: 0;
        transform: translate3d(20rem/$r, 0, 0);
      }
    }
    .add {
      @extend %display, %font;
      padding-right: 20rem/$r;
    }
  }
</style>

 


